@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
div#ToDoWrapper {
  background: #395FA6;
  color: white;
  margin-top: 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 15px;
  border-radius: 8px;
}

div#ToDoDescription {
  display: flex;
  justify-content: space-between;

}

div#ToDoDescription > button {
  background: none;
  margin-top: -10px;  
  margin-right: -10px;  
}

div#ContentToDo {
  display: flex;
  flex: 1 1;
  flex-direction: row;
  align-items: center;
}

div#TagDescription {
  display: flex;
  flex: 1 1;
  flex-direction: row;
}

p {
  font-size: 18px;
  margin-left: 5px;
}

p#NoChecked {
  text-decoration: line-through;
}

span {
  background: #234992;
  padding: 5px;
  border-radius: 4px;
  margin-right: 5px;
  margin-top: 10px;
}
div#List {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 20px;
  list-style: none;
}

div#container {
  max-width: 600px;
  margin: 30px auto;
  padding: 30px;
}

div#container > h1 {
  font-size: 34px;
}

input {
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  font-size: 4px;
  height: 25px;
  display: flex;
  padding: 5px;
  justify-content: center;
  align-items: stretch;
  min-width: 0;
}

form#Submit {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  margin-top: 20px;
}

form#Submit > div {
  display: flex;
  flex: 1 1;
  flex-direction: row;
  margin-top: 5px;
  flex-wrap: wrap;
}

form#Submit > div > input {
  display: flex;
  flex: 1 1;
  flex-direction: row;
  align-items: stretch;
}

form#Submit > div > input + button {
  margin-left: 10px;
}

form#Search {
  display: flex;
  flex: 1 1;
  flex-direction: row;
  margin-top: 15px;
}

form#Search > input {
  display: flex;
  flex: 1 1;
  flex-direction: row;
  align-items: stretch;
  max-width: 250px;
}

form#Search > button {
  margin-left: 10px;
  width: 25px;
}

button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 25px;
  width: 25px;
  background: #323232;
  border: none;
  border-radius: 4px;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

button:hover {
  opacity: 0.8;
}
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px Roboto, sans-serif;
  background: #eee;
  -webkit-font-smoothing: antialiased;
}

input, button, textarea {
  font: 400 14px Roboto, sans-serif;
}

button {
  cursor: pointer;
}

